.test-series-review-wrapper {
  .total-score-wrapper {
    background: linear-gradient(120deg, #027eff, #89e8fe);
    padding: 10px;
    max-width: 200px;
    text-align: center;
    margin: 15px auto 40px;
    border-radius: 8px;
    color: #fff;
    font-weight: 600;
  }
  .box {
    border: 1px solid #80beff;
    border-radius: 6px;
    padding: 15px;
    margin-right: 60px;
    font-weight: 600;
    color: #333;
    .percent {
      color: #ffcfa3;
    }
    &.skipped {
      button {
        border: 1px solid #ffc38b;
        color: #333;
        border-radius: 6px !important;
        background-color: transparent;
        padding: 6px 25px !important;
        text-transform: initial;
        &:not(.disabled):focus,
        &:not(.disabled):active {
          border: 1px solid #ffc38b;
          background-color: transparent;
          box-shadow: none !important;
          color: #333;
        }
      }
    }
    .separator {
      padding: 10px;
      &.bottom {
        border-bottom: 2px solid #ddd;
      }
      &.left {
        border-left: 2px solid #ddd;
      }
    }
    .chart-wrapper {
      height: 100px;
    }
    .wrapper {
      margin: 10px 0 15px 0;
      font-weight: normal;
      &:last-child {
        margin-bottom: 0;
      }
      .correct,
      .wrong {
        display: inline-block;
        width: 15px;
        height: 15px;
        vertical-align: text-top;
        margin: 3px 10px 0 0;
      }
      .correct {
        background-color: #56d686;
      }
      .wrong {
        background-color: #f14c0f;
      }
    }
    .prgress-wrapper {
      .label {
        margin-right: 15px;
      }
      margin: 15px 0;
    }
  }
  .btn-wrapper {
    margin: 60px 0;
    button {
        background: linear-gradient(120deg, #027eff, #89e8fe);
        border: none;
        text-transform: initial;
        border-radius: 5px !important;
        &:focus, &:active {
            background: linear-gradient(120deg, #027eff, #89e8fe);
            box-shadow: none;
        }
    }
}
}
