
.header-component {
    .user-name {
        display: flex;
        align-items: center;
        border: 1px solid #30A2FF;
        border-radius: 20px;
    }
    .sidebar-nav-container {
        position: absolute;
        top: 0;
        left: 0;
        width: 100vw;

        &.active {
            background-color: white;
            border: 1px solid gray;
        }
    }
}
.position-relative{
    position: relative;
} 

.child-change-course{
    position: absolute;
    top: 100%;
    width: 100%;
    border: 1px solid lightgrey !important;
    border-radius: 5px !important;
    font-size: 0.8em;
    padding: 4px;

    div:hover{
        text-decoration: underline;
        cursor: pointer;
    }
}

.position-relative:hover .hover-active{
    display: block !important;

}