.social-icon {
    height: 35px;
    width: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    cursor: pointer;
    background-color: white;
    img {
        height: 25px;
        width: 25px;
    }
}

.carousel.carousel-slider {
    display: flex; 
    .prev-btn, .next-btn {
        width: auto;
        height: 24px;
        align-self: center;
        cursor: pointer;
        pointer-events: unset;
    }
    .carousel-status {
        display: none;
    }
    .control-dots {
        bottom: -10px !important;
        .dot {
            border: 1px solid black !important;
            height: 14px !important;
            width: 14px !important;
            box-shadow: none !important;
            &.selected {
                background-color: black !important;
            }
        }
    }       
}


img.cross-icon {
    position: absolute;
    right: 20px;
    top: 20px;
}


.otp-input {
    @media screen and (min-width: 768px) {
        width: 60px !important;
    }
    @media screen and (max-width: 768px) {
        width: 30px !important;
        height: 30px;
        padding: 2px !important;
    }
}

.faq-box {
    border-radius: 20px;
    overflow: hidden;
    .faq-container {
        height: 50vh;
        overflow-y: auto;
    }
}