.test-series-question-wrapper {
  .top-section {
    font-weight: 600;
    button.section {
      background-color: transparent;
      text-transform: initial;
      color: #333;
      padding: 5px 15px !important;
      border-radius: 5px !important;
      margin-right: 15px;
      &:focus,
      &:active {
        background-color: #027eff;
        box-shadow: none;
        border-color: #027eff;
        color: #fff;
      }
      &.active {
        background-color: #027eff;
        color: #fff;
        border-color: #027eff;
      }
    }
  }
  .question-details {
    margin: 20px 0;
    font-weight: 600;
    ul {
      li {
        list-style-type: none;
        margin: 10px 0;
      }
    }
  }
  .btn-wrapper {
    margin-top: 15px;
    button {
      background: transparent;
      color: #027eff;
      border: 1px solid #027eff;
      text-transform: initial;
      border-radius: 5px !important;
      &:focus,
      &:active {
        color: #fff;
        box-shadow: none;
        background: linear-gradient(120deg, #027eff, #89e8fe);
      }
      &.save {
        color: #fff;
        background: linear-gradient(120deg, #027eff, #89e8fe);
      }
    }
  }
  .right-wrapper {
    .user-name {
      font-weight: 600;
    }
    .user-icon {
      display: inline-block;
      width: 50px;
      height: 50px;
      background-color: #ddd;
      vertical-align: middle;
      border-radius: 100%;
      margin: 10px;
    }
    background-color: #f1f5f6;
    .status-wrapper {
      width: 50%;
      display: inline-block;
      vertical-align: middle;
      font-size: 11px;
      font-weight: 600;
      margin: 10px 0;
      &:last-child {
        width: 100%;
      }
      span {
        display: inline-block;
        width: 25px;
        vertical-align: middle;
        height: 25px;
        border-radius: 100%;
        margin-right: 5px;
        &.grey {
          background-color: #e6e5e5;
        }
        &.red {
          background-color: #d84545;
        }
        &.green {
          background-color: #319e31;
        }
        &.blue {
          background-color: #027eff;
        }
        &.small-green {
          width: 15px;
          height: 15px;
          left: -15px;
          top: 10px;
          position: relative;
          margin: 0;
          background-color: #319e31;
        }
      }
    }
    button.save {
      background: linear-gradient(120deg, #027eff, #89e8fe);
      border: none;
      text-transform: initial;
      border-radius: 5px !important;
      &:focus,
      &:active {
        background: linear-gradient(120deg, #027eff, #89e8fe);
        box-shadow: none;
      }
    }
    .attended-qstn-wrapper {
      max-width: 245px;
      margin: 30px auto;
      .title {
        background: linear-gradient(120deg, #027eff, #89e8fe);
        padding: 5px;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
      }
      .qustns {
        background-color: #fff;
        .qstn-no {
          display: inline-block;
          width: 20px;
          height: 20px;
          line-height: 20px;
          border: 1px solid #ddd;
          text-align: center;
          border-radius: 100%;
          margin: 10px 5px;
        }
      }
    }
  }
}
