.anatomy-exam-wrapper {
  .exam-title {
    // margin: 20px 0;
    color: #419eff;
  }
  .hide{
    display: none;
  }

  
  ul.pagination>li>a {
    margin-left: -1px;
    padding: 0.375rem 0.75rem;
      position: relative;
      display: block;
      color: #0d6efd;
      text-decoration: none;
      background-color: #fff;
      border: 1px solid #dee2e6;
      transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
/* qbank questionanswer */
.correct_ans{
  background-color: green;
}

.danger_ans{
  background-color: red;
}

/*end  qbank questionanswer */
  .time {
    display: inline-block;
    width: 50px;
    border: 1px solid #75b9ff;
    height: 50px;
    line-height: 50px;
    border-radius: 100px;
    text-align: center;
    font-weight: 600;
    margin-left: 15px;
    background-color: #f1f5f6;
  }
  .question-wrapper {
    padding-top: 20px;
    .question {
      .question-no {
        width: 25px;
        height: 25px;
        line-height: 25px;
        background-color: #89e8fe;
        display: inline-block;
        font-weight: 600;
        box-shadow: 1px 2px 3px 1px #ddd;
        border-radius: 6px;
      }
      .question-text {
        font-weight: bold;
        color: #333;
        margin-bottom: 15px;
      }
      .options {
        form {
          .form-check.form-check-inline {
            input {
              margin-top: 3px;
            }
            label {
              margin-bottom: 10px;
            }
          }
        }
      }
      .notes-wrapper {
        margin-top: 5px;
        .percentage {
          background: linear-gradient(120deg, #027eff, #89e8fe);
          display: inline-block;
          width: 40px;
          height: 40px;
          line-height: 40px;
          font-weight: bold;
          border-radius: 100px;
          color: #fff;
          margin-right: 5px;
        }
        .notes {
          border: 2px solid #80beff;
          padding: 10px;
          border-radius: 8px;
          margin-top: 15px;
        }
      }
    }
    .pagination {
      margin: 30px 0;
      ul.pagination {
        .page-item {
          margin: 0 8px;
          &:first-child,
          &:last-child {
            margin: 0 10px;
            a {
              background-color: #027eff;
              color: #fff;
              &:focus,
              &:active,
              &:hover {
                color: #fff !important;
              }
            }
          }
          &:first-child {
            margin-left: 0;
          }
          a {
            border: none;
            box-shadow: 1px 2px 3px 1px #ddd;
            border-radius: 6px;
            font-weight: 600;
            color: #333;
          }
        }
      }
    }
    button {
      border: none;
      border-radius: 6px !important;
      background-color: #fc3838;
      padding: 6px 25px !important;
      text-transform: initial;
      &:not(.disabled):focus, &:not(.disabled):active {
        box-shadow: none;
        background-color: #fc3838;
      }
      &.btn-submit {
        background: linear-gradient(120deg, #027eff, #89e8fe);
        &:not(.disabled):focus, &:not(.disabled):active {
          background: linear-gradient(120deg, #027eff, #89e8fe);
        }
      }
    }
  }
  .explanations-wrapper {
    padding: 15px 20px;
    box-shadow: -1px 1px 5px 1px #ddd;
    border-radius: 20px;
    max-height: 400px;
    overflow: auto;
    .explanation-text {
      margin: 25px 0;
    }

  
    button {
      border: 1px solid #ffc38b;
      border-radius: 6px !important;
      background-color: transparent;
      padding: 6px 25px !important;
      text-transform: initial;
      color: #ffc38b;
      font-weight: normal !important;
      &:not(.disabled):focus, &:not(.disabled):active {
        border: 1px solid #ffc38b;
        background-color: transparent;
        box-shadow: none !important;
        color: #ffc38b;
      }
    }
  }
}
