.anatomy-wrapper {
  .accordion-item {
    border: none;
    margin: 10px 0;
    .accordion-button {
      background-color: #eff7ff;
      padding: 0 10px 0 0 !important;
      box-shadow: 0px 1px 2px 2px #ddd;
      border-radius: 10px !important;
      .accordian-title {
        display: inline-block;
        background-color: white;
        padding: 10px;
        border-radius: 10px;
        box-shadow: 1px 2px 0px 1px #ddd;
      }
    }
    .hide{
      display: none;
    }

    .accordion-body {
      ul {
        li.list-item {
          margin: 10px 0;
          list-style: none;
          border-bottom: 1px solid #ddd;
          padding-bottom: 8px;
          .left-title {
            font-weight: 600;
          }
          .right-text {
            font-weight: 600;
            color: #888;
          }
          form {
            margin-top: 10px;
          }
          input {
            margin-top: 3px;
          }
          button.start-exam {
            border: none;
            border-radius: 6px !important;
            background: linear-gradient(120deg, #027EFF, #89E8FE);
            padding: 6px 25px !important;
            text-transform: initial;
            &:focus {
                box-shadow: none;
            }
          }
        }
      }
    }
  }
}
