.anatomy-question-details-wrapper {
  color: #454a51;
  padding: 30px;
  .question-title {
    margin-bottom: 10px;
    .qstn-no {
      font-weight: bold;
      padding: 0 5px 0 0;
      border-right: 2px solid #027eff;
      margin-right: 7px;
      color: #027eff;
    }
  }
  form {
    margin-bottom: 40px;
    .form-check.form-check-inline {
      input {
        margin-top: 3px;
      }
      label {
        font-weight: 600;
      }
    }
  }
  button.start-exam {
    border: none;
    border-radius: 6px !important;
    background: linear-gradient(120deg, #027eff, #89e8fe);
    padding: 6px 25px !important;
    text-transform: initial;
    &:focus, &:active {
      box-shadow: none;
      background: linear-gradient(120deg, #027eff, #89e8fe) !important;
    }
  }
  .attempts-wrapper {
    margin: 30px 0;
    .title {
        font-weight: 600;
        &.clear-history {
            color: #eb490d;
            cursor: pointer;
        }
    }
    table {
        width: 100%;
        margin: 15px 0;
        text-align: center;
        tr {
            border-bottom: 1px solid #888;
            margin-bottom: 8px;
            &:first-child,&:last-child {
                border: none;
            }
            td {
                padding: 5px 0;
            }
        }
    }
    .instructions-wrapper {
        margin-top: 10px;
        .desc {
            margin: 20px 0;
        }
        ul {
           list-style: decimal;
           li {
            margin: 10px 0;
           }
        }
    }
  }
}
