.question-bank-wrapper {
    .questionCard-wrapper {
        width: 23%;
        margin: 1%;
        box-shadow: 1px 3px 7px 0px #ddd;
        border-radius: 20px;
        @media screen and (max-width: 767px) {
            width: 100%;
        }
        .questionCard {
            padding: 5px;
            .qstn-image {
                @media screen and (max-width: 767px) {
                    width: 50%;
                }
            }
            .qstn-detail {
                border: 2px solid #ffc98b;
                border-radius: 20px;
                @media screen and (max-width: 767px) {
                    width: 50%;
                }
                div {
                    margin: 8px 0;
                    &.title {
                        color: #3196ff;
                        font-weight: 600;
                    }
                    &.anatomy-complete {
                        font-weight: bold;
                    }
                }
            }
        }
    }
}