.dashboard-page {
    .wrapper {
        display: flex;
        align-items: stretch;
    }

    #sidebar {
        min-width: 250px;
        max-width: 250px;
        background: #EFF7FF;
        color: #fff;
        transition: all 0.1s ease-in-out;
        position: relative;
    }

    #sidebar.active {
        min-width: 80px;
        max-width: 80px;
        text-align: center;
    }

    #sidebar.active .sidebar-header h3,
    #sidebar.active .CTAs {
        //display: none;
    }

    #sidebar.active .sidebar-header strong {
        display: block;
    }

    #sidebar ul li a {
        text-align: left;
    }

    #sidebar.active ul li a {
        padding: 20px 10px;
        text-align: center;
        font-size: 0.85em;
    }

    #sidebar.active ul li a i {
        margin-right: 0;
        display: block;
        font-size: 1.8em;
        margin-bottom: 5px;
    }

    #sidebar.active ul ul a {
        padding: 10px !important;
    }

    #sidebar.active .dropdown-toggle::after {
        top: auto;
        bottom: 10px;
        right: 50%;
        -webkit-transform: translateX(50%);
        -ms-transform: translateX(50%);
        transform: translateX(50%);
    }

    #sidebar .sidebar-header {
        padding: 20px;
        background: #6d7fcc;
    }

    #sidebar .sidebar-header strong {
        display: none;
        font-size: 1.8em;
    }

    #sidebar ul.components {
        padding: 20px;
        // border-bottom: 1px solid #47748b;
        position: relative;
    }

    #sidebar ul li a {
        padding: 8px 16px;
        font-size: 1.1em;
        display: block;
        color: #000;
        margin-bottom: 15px;
    }

    #sidebar ul li a img {
        -webkit-filter: (100%);
        /* Safari 6.0 - 9.0 */
        filter: grayscale(100%);
        -webkit-transition: all .1s ease-in-out;
        margin-right: 30px;
    }

    #sidebar ul li a:hover {
        color: #000 !important;
        background: rgb(2, 126, 255);
        background: linear-gradient(90deg, rgba(2, 126, 255, 1) 0%, rgba(2, 126, 255, 1) 25%, rgba(255, 255, 255, 1) 25%, rgba(255, 255, 255, 1) 100%);
        border-radius: 30px;
        box-shadow: rgba(34, 44, 54, 0.2) 0px 8px 24px;
    }

    #sidebar ul li a i {
        margin-right: 10px;
    }

    #sidebar ul li a:hover img,
    #sidebar ul li.active>a img {
        filter: invert(1);
        -webkit-filter: invert(1);
        -webkit-transform: scale(1.01);
    }

    #sidebar ul li.active>a,
    a[aria-expanded="true"] {
        color: #000;
        background: rgb(2, 126, 255);
        background: linear-gradient(90deg, rgba(2, 126, 255, 1) 0%, rgba(2, 126, 255, 1) 25%, rgba(255, 255, 255, 1) 25%, rgba(255, 255, 255, 1) 100%);
        border-radius: 30px;
        box-shadow: rgba(34, 44, 54, 0.2) 0px 8px 24px;
    }

    #sidebar.active ul.components {
        padding: 30px 0;
    }

    #sidebar.active ul li a:hover,
    #sidebar.active ul li.active>a {
        background: rgb(2, 126, 255);
        color: #FFF !important;
    }

    #sidebar.active ul li a img {
        display: block;
        margin: 0 auto 5px auto;
    }

    a[data-toggle="collapse"] {
        position: relative;
    }

    .dropdown-toggle::after {
        display: block;
        position: absolute;
        top: 50%;
        right: 20px;
        transform: translateY(-50%);
    }

    ul ul a {
        font-size: 0.9em !important;
        padding-left: 30px !important;
        background: #6d7fcc;
    }

    ul.CTAs {
        padding: 20px;
    }

    ul.CTAs a {
        text-align: center;
        font-size: 0.9em !important;
        display: block;
        border-radius: 5px;
        margin-bottom: 5px;
    }

    a.download {
        background: #fff;
        color: #7386D5;
    }

    #sidebar .collapsebtn {
        position: absolute;
        top: 0;
        right: 0;
        z-index: 1;
    }

    /* ---------------------------------------------------
        CONTENT STYLE
    ----------------------------------------------------- */

    #content {
        width: 100%;
        padding: 20px;
        min-height: 100vh;
        transition: all 0.1s;
    }

    /* ---------------------------------------------------
        MEDIAQUERIES
    ----------------------------------------------------- */

    @media (max-width: 768px) {
        #sidebar {
            min-width: 80px;
            max-width: 80px;
            text-align: center;
            margin-left: -80px !important;
        }

        .dropdown-toggle::after {
            top: auto;
            bottom: 10px;
            right: 50%;
            -webkit-transform: translateX(50%);
            -ms-transform: translateX(50%);
            transform: translateX(50%);
        }

        #sidebar.active {
            margin-left: 0 !important;
        }

        #sidebar .sidebar-header h3,
        #sidebar .CTAs {
            display: none;
        }

        #sidebar .sidebar-header strong {
            display: block;
        }

        #sidebar ul li a {
            padding: 20px 10px;
        }

        #sidebar ul li a span {
            font-size: 0.85em;
        }

        #sidebar ul li a i {
            margin-right: 0;
            display: block;
        }

        #sidebar ul ul a {
            padding: 10px !important;
        }

        #sidebar ul li a i {
            font-size: 1.3em;
        }

        #sidebar {
            margin-left: 0;
        }

        #sidebarCollapse span {
            display: none;
        }

    }

    .powerpack {
        background: rgb(11, 133, 255);
        background: linear-gradient(90deg, rgb(11, 133, 255) 0%, rgb(98, 202, 254) 100%);
        width: auto;
        margin: auto;
        padding: 20px;
        border-radius: 30px;
        text-align: center;
    }

    ul.profile-nav {
        margin: 0;
        padding: 0;
        list-style-type: none;
        background: #eff7ff;
        border-radius: 30px;
        box-shadow: rgba(34, 44, 54, 0.2) 0px 8px 24px;
        display: inline-block;
    }

    ul.profile-nav li {
        display: inline-block;
        margin-right: 15px;
    }

    ul.profile-nav li:last-child {
        margin-right: 0px;
    }

    ul.profile-nav li a {
        display: block;
        padding: 8px 30px;
    }

    ul.profile-nav li a:hover{
        background-color: #ff890247;
        border-radius: 30px;
    }
    ul.profile-nav li a.active {
        background-color: #ff8902;
        border-radius: 30px;
        color: #FFF !important;
    }

    .form-control {
        display: block;
        width: 100%;
        padding: 0.75rem 0.75rem;
        font-size: 1rem;
        font-weight: 400;
        line-height: 1.5;
        color: #212529;
        background-color: #fff;
        background-clip: padding-box;
        border: 1px solid #ced4da;
        appearance: none;
        border-radius: 0.25rem;
        transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
    }

    .form-select {
        padding: 0.75rem 0.75rem;
    }

    .roundedcorner {
        border-radius: 0.25rem !important;
    }
    .cust-btn{
        border-radius: 50px;
         box-shadow: 1px 1px 5px 4px rgba(0,0,0,0.17);
    }
}