.notification {
    // height: 50px;
    max-width: 300px;
    position: fixed;
    font-size: 12px;
    right: 0;
    top: 100px;
    z-index: 10000;
}

.loading-screen {
    display: none;
    height: 100vh;
    width: 100vw;
    background: rgba(0, 0, 0, .6);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000000;
}
.active {
    display: flex;
    align-items: center;
    justify-content: center;
}
.active-tab{
    color: #000 !important;
    background: rgb(2, 126, 255);
    background: linear-gradient(90deg, rgba(2, 126, 255, 1) 0%, rgba(2, 126, 255, 1) 25%, rgba(255, 255, 255, 1) 25%, rgba(255, 255, 255, 1) 100%);
    border-radius: 30px;
    box-shadow: rgba(34, 44, 54, 0.2) 0px 8px 24px;
}
.active-tab-shrinked{
    color: #000 !important;
    background: rgb(2, 126, 255);
    border-radius: 30px;
    box-shadow: rgba(34, 44, 54, 0.2) 0px 8px 24px;
}

.alert-section {
    height: 30px;
}

.alert {
    padding: 5px 20px !important;
    width: max-content;
    border: none;
    font-size: 12px;
}

.up50 {
    position: relative;
    top: -50px;
}

a { 
    text-decoration: none !important;
    &:hover {
        text-decoration: none !important;
    }
}
.text-right {
    text-align: right;
    .progress-bar {
        text-align: right;
    }
}
.text-left {
    text-align: left;
    .progress-bar {
        text-align: left;
    }
}
.green-dot {
    background: chartreuse;
    height: 8px;
    width: 8px;
    border-radius: 50%;
}

.fixed {
    position: fixed;
}

.bg-main{
    background-image: url("./../assets/Back\ Ground.png");
    background-position: 100% 0%;
    background-size: cover;
}

.sm-font{
    font-size: 0.75em;
}

.border-sharp{
    border-radius: 5px !important;
}
.max-w-550{
    max-width: 550px;
}

.ml-33{
    margin-left: 33px;
}

.m-700{
    width: 700px;
    max-width: 700px;
}
.max-h-200{
    max-height: 200px;
    overflow-y: scroll;
}

.relative{
    position: relative;
}
.center{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
.two-00{
    height: 200px;
    width: 200px;
}

.min-h-200{
    min-height: 200px;
}
.roundedcorner1{
    border-radius: 0.4rem !important;
}
.invert img{
    -webkit-transform:rotate(180deg);
    -moz-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    transform: rotate(180deg);
}
.w-200{
 min-width: 200px !important;
} 

.dashed-border{
    border-style: dashed !important;
}
.max-w-400{
    max-width: 400px;
    margin: 10px auto;
}