.test-series-anatomy-info-wrapper {
  .section {
    margin-bottom: 20px;
    .qstn-status-wrapper {
      background-color: #fbfbfb;
      padding: 10px;
      .qstn-status-box {
        width: 20%;
        display: inline-block;
        border-right: 1px solid #f1f1f1;
        padding: 20px;
        vertical-align: text-top;
        font-size: 11px;
        min-height: 125px;
        &:last-child {
          border: none;
        }
        span {
          display: inline-block;
          width: 30px;
          height: 30px;
          border-radius: 100%;
          &.grey {
            background-color: #e6e5e5;
          }
          &.red {
            background-color: #d84545;
          }
          &.green {
            background-color: #319e31;
          }
          &.blue {
            background-color: #027eff;
          }
          &.small-green {
            width: 20px;
            height: 20px;
            left: -12px;
            top: 5px;
            position: relative;
            background-color: #319e31;
          }
        }
      }
    }
  }
  form {
    input.form-check-input {
      margin-top: 3px;
    }
  }
  .btn-wrapper {
    margin-top: 15px;
    button.start {
      background: transparent;
      color: #027eff;
      border: 1px solid #027eff;
      text-transform: initial;
      border-radius: 5px !important;
      &:focus,
      &:active {
        color: #fff;
        background: linear-gradient(120deg, #027eff, #89e8fe);
        box-shadow: none;
      }
    }
  }
}
