.video-page {
    .video-card {
        box-shadow: rgba(34, 44, 54, 0.2) 0px 8px 24px;
        border-radius: 20px;
        cursor: pointer;
        border: 1px solid transparent;
        margin-bottom: 15px;
    }

    .video-card:hover {
        border: 1px solid #042ce0;
    }

    .video-card p {
        margin-bottom: 0px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        width: 100%;
    }

    .video-card>.header {
        position: relative;
        padding: 5px;
        text-align: center;
    }

    .video-card>.header p {
        margin-bottom: 0;
        font-weight: 600;
    }

    .video-card>.vthumbnail {
        position: relative;
        width: 100%;
        height: 150px;
    }

    .video-card .vthumbnail2 {
        position: relative;
        width: 64px;
        height: 64px;
        border-radius: 10px;
    }

    .video-card .vthumbnail2 img {
        object-fit: cover;
        height: 64px;
        border-radius: 10px;
    }

    .video-card>.vthumbnail .overlay {
        position: absolute;
        width: 100%;
        background-color: rgba(2, 126, 255, 0.7);
        bottom: 0;
        padding: 10px;
        color: #FFF;
    }

    .video-card>.vthumbnail img {
        object-fit: cover;
        width: 100%;
        height: 150px;
    }

    .video-card .cardfooter {
        position: relative;
        padding: 10px;
        text-align: center;
        background-color: transparent;
    }

    .playbtn {
        position: absolute;
        top: 15px;
        right: 20px;
    }
}
.relative{
    position: relative;
}

.top-right{
    position: absolute;
    top: 0px;
    right: 10px;
    font-size: 30px;
    z-index: 999999;
}

.center{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.f-40{
    font-size: 40px;
}

.pointer{
    cursor: pointer;
}