.test-instructions-wrapper {
    h5.instruction-title {
        font-weight: 600;
        margin: 15px 0;
    }
    ul {
        li {
            list-style-type: decimal;
            margin: 5px 0;
        }
    }
    .btn-wrapper {
        margin: 15px 0;
        button.start {
            background: linear-gradient(120deg, #027eff, #89e8fe);
            border: none;
            text-transform: initial;
            border-radius: 5px !important;
            &:focus, &:active {
                background: linear-gradient(120deg, #027eff, #89e8fe);
                box-shadow: none;
            }
        }
    }
}