.btn-circle.btn-xl {
    width: 70px;
    height: 70px;
    position: absolute;
    padding: 10px 15px !important;
    border-radius: 35px;
    border-width: .3rem;
    font-weight: bolder !important;
    font-size: 12px;
    text-align: center !important;
    cursor: pointer;
    margin-top: -100px;
    left: 160px;
    z-index: 2 !important;
    position: relative;
    background-color: #fff;

}

.btn-circle.btn-xl:hover {
    color: black;
}

.dropdown-toggle[aria-expanded="true"]:after {
    transform: rotate(180deg);
}

/*for animation*/
.dropdown-toggle:after {
    transition: 0.7s;
}

.planCard-section {
    box-shadow: -4px 2px 31px 10px rgba(0, 0, 0, 0.35)
}

.plancard-select .plan-select {
    border-radius: none !important;
    width: 100vw !important;
    height: 5vh;
    border: 2px solid rgb(200, 157, 157) !important;
}

.plan-check {
    margin: 20px 30px !important;
}

.planCard-section .dropdown-menu {
    border: 1px solid rgb(11, 133, 255);
    width: 95%;
    border-radius: 10px;
}

.planCard-section .dropdown-toggle {
    width: 100%;

    border: 2px solid rgb(11, 133, 255);
    border-radius: 5px !important;
}

.plan-card-profile .wrapper {
    padding: 100px;
    position: relative
}

.plan-card-profile .backcover {
    position: absolute;
    background-color: blue;
    width: 200px;
    height: 180px;
    border-radius: 50%;
    top: -190px;
    right: -55px;
}

.plan-card-profile .image--cover {
    width: 180px;
    height: 160px;
    border-radius: 50%;
    margin: 20px;
    position: absolute;
    object-fit: cover;
    object-position: center right;
    top: -200px;
    right: -70px;

}
.mw-30{
    max-width: 20px;
}