.test-series-wrapper {
    .tab-wrapper {
        margin: 20px 0;
        ul.profile-nav {
            li {
                a {
                    color: #3d9cff;
                    font-weight: 600;
                    padding: 6px 25px;
                }
            }
        }
        select {
            padding: 7px 12px;
            color: #3d9cff;
            font-weight: 600;
            border: none;
            box-shadow: 1px 3px 7px 0px #ddd;
            border-radius: 5px;
            &:focus {
                outline: none;
            }
        }
    }
    .qstn-available {
        color: #419eff;
        font-weight: 600;
    }
  .anatomy-wrapper {
    margin: 20px 30px 10px 10px;
    padding: 5px 10px;
    box-shadow: 1px 3px 7px 0px #ddd;
    border-radius: 8px;
    .no-of-questions {
        border: 1px solid #75b9ff;
        border-radius: 5px;
        padding: 10px 5px;
        font-weight: 600;
        .no {
            span {
                font-size: 11px;
            }
        }
    }
    .details {
        .heading {
            color: #419eff;
        }
        .duration {
            color: #419eff;
        }
        .date {
            margin: 5px 0;
        }
    }
    .btn-wrapper {
        button {
            background: linear-gradient(120deg, #027eff, #89e8fe);
            border: none;
            text-transform: initial;
            padding: 5px 12px !important;
            border-radius: 5px !important;
            &:focus, &:active {
                background: linear-gradient(120deg, #027eff, #89e8fe);
                box-shadow: none;
            }
        }
    }
  }
}
