.home {
    .demo-video-btn {
        position: relative;
        display: flex;
        font-size: 18px;
        @media screen and (min-width: 768px) {
            margin-left: 0px;
        }
        @media screen and (max-width: 768px) {
            margin-left: 25px;
        }
        .demo-video-btn-text {
            position: absolute;
            color: white;
            left: 45%;
            top: 50%;
            transform: translate(-50%,-50%);
        }
    }
    
    .image-box {
        max-width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        img {
            max-width: 100%;
            max-height: 100%;
        }
    
        &.box-1 {
            height: 200px;
        }
        &.box-2 {
            height: 250px;
        }
    }
    
    .course-name {
        @media screen and (min-width: 768px) {   
            height: 8rem;
            width: 8rem;
            border-radius: 25px;
        }
        @media screen and (max-width: 768px) {
            padding: 10px 20px;
            border-radius: 5px;
            color: white;
        }
        background-color: #30A2FF;
        font-weight: bold;
        font-size: 16px;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
    }
    .notes-image {
        border-radius: 15px;
        border: 2px solid orange;
    }
}
.rank {
    position: absolute;
    top: 16px;
    right: 24px;
}
.avatar {
    height: 45px !important;
    width: 45px !important;
}

.w-200{
    width: 200px;
}