ul.custom-module-profile-nav {
    margin: 0 10px;
    padding: 0 0;
    list-style-type: none;
    background: #eff7ff;
    border-radius: 30px;
    box-shadow: rgba(34, 44, 54, 0.2) 0px 8px 24px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-size: 1.5rem;
    width:40vw;
}
ul.mode li a{
   padding:10px 60px !important;
}

ul.custom-module-profile-nav li {
    display: inline-block;
    margin-right: 15px;
    font-size: 1.5rem;
    padding:0 0px;
  
   
}
ul.custom-module-profile-nav li a{
    //width:10vw;
    margin:0 10px ;
}

ul.custom-module-profile-nav li:last-child {
    margin-right: 0px;
}

ul.custom-module-profile-nav li a {
    display: block;
    padding: 8px 30px;
}

ul.custom-module-profile-nav li a:hover,
ul.custom-module-profile-nav li a.active {
    background-color: #ff8902;
    border-radius: 30px;
    color: #FFF !important;
}

.form-control {
    display: block;
    width: 100%;
    padding: 0.75rem 0.75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    appearance: none;
    border-radius: 0.25rem;
    transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
}

.form-select {
    padding: 0.75rem 0.75rem;
}

.roundedcorner {
    border-radius: 0.25rem !important;
}
