// Oxygen
@font-face {
    font-family: "Oxygen";
    font-style: normal;
    font-weight: bold;
    src: local("Oxygen"), url("./fonts/Oxygen/Oxygen-Bold.ttf");
}
@font-face {
    font-family: "Oxygen";
    font-style: normal;
    font-weight: lighter;
    src: local("Oxygen"), url("./fonts/Oxygen/Oxygen-Light.ttf");
}
@font-face {
    font-family: "Oxygen";
    font-style: normal;
    font-weight: normal;
    src: local("Oxygen"), url("./fonts/Oxygen/Oxygen-Regular.ttf");
}

.font-oxygen {
    font-family: 'Oxygen';
    &.light {
        font-weight: lighter;
    }
    &.bold {
        font-weight: bold;
    }
}

// Roboto
@font-face {
    font-family: "Roboto";
    font-style: normal;
    font-weight: normal;
    src: local("Roboto"), url("./fonts/Roboto/Roboto-Regular.ttf");
}
@font-face {
    font-family: "Roboto";
    font-style: normal;
    font-weight: bold;
    src: local("Roboto"), url("./fonts/Roboto/Roboto-Bold.ttf");
}
@font-face {
    font-family: "Roboto";
    font-style: normal;
    font-weight: lighter;
    src: local("Roboto"), url("./fonts/Roboto/Roboto-Light.ttf");
}
@font-face {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 600;
    src: local("Roboto"), url("./fonts/Roboto/Roboto-Medium.ttf");
}

.font-roboto {
    font-family: 'Roboto';
    &.light {
        font-weight: lighter;
    }
    &.bold {
        font-weight: bold;
    }
    &.medium {
        font-weight: 600;
    }
}

.font-light {
    font-weight: lighter;
}
.font-bold {
    font-weight: bold;
}
.font-medium {
    font-family: 'Roboto';
    font-weight: 600;
}

