.course-page {
    .course-faculty-img {
        border-radius: 50%;
        height: 180px;
        width: 180px;
        display: flex;
        flex-direction: column;
        // justify-content: center;
        align-items: center;
        img {
            height: 100px;
            width: 100px;
        }
    }

    .course-image-box {
        height: 120px;
        display: flex;
        align-items: center;
        .course-image {
            max-height: 120px;
            max-width: 120px;
        }
    }
}