.test-series-anatomy-wrapper {
    .qstn-available {
        color: #419eff;
        font-weight: 600;
    }
  .anatomy-wrapper {
    margin: 20px 30px 10px 10px;
    padding: 5px 10px;
    box-shadow: 1px 3px 7px 0px #ddd;
    border-radius: 8px;
    .no-of-questions {
        border: 1px solid #75b9ff;
        border-radius: 5px;
        padding: 10px 5px;
        .no {
            color: #419eff;
            font-weight: 600;
        }
    }
    .details {
        .duration {
            color: #419eff;
        }
        .date {
            margin: 5px 0;
        }
    }
    .btn-wrapper {
        button {
            background: linear-gradient(120deg, #027eff, #89e8fe);
            border: none;
            text-transform: initial;
            padding: 5px 12px !important;
            border-radius: 5px !important;
            &:focus, &:active {
                background: linear-gradient(120deg, #027eff, #89e8fe);
                box-shadow: none;
            }
        }
    }
  }
}
