  .max-400{
    max-height: 250px;
    object-fit: cover;
  }
  
  .max-145{
    max-height: 163px;
    object-fit: contain;
  }
  
  .text-card-sm{
    font-size: 10px;
    margin: 0px auto;
  }
  
  .custom-button{
    background: linear-gradient(90deg,  rgb(4, 141, 246), rgb(50, 97, 147));
    border-radius: 5px;
    width: 140px;
    padding: 10px;
  }
  
  .custom-button-oval{
    background: linear-gradient(90deg,  rgb(4, 141, 246), rgb(50, 97, 147));
    border-radius: 5px;
    min-width: 90px;
    padding: 10px;
    border-radius: 40px !important;
  }
  
  .custom-button-oval1{
    background: linear-gradient(90deg,  rgb(4, 141, 246), rgb(50, 97, 147));
    border-radius: 5px;
    min-width: 200px;
    padding: 10px;
    border-radius: 40px !important;
  }
  
  .pointer{
    cursor: pointer;
  }
  
  .logo{
    max-height: 80px;
  }
  
  .icon{
    max-width: 30px;
  }

  .light-border{
    border: 1px solid rgba(0, 0, 0, 0.094) !important;
    border-radius: 4px !important;
  }